


















































import Vue from 'vue';
import { instance as axios } from '@/services/axios';
import { apiListBalita, apiDeleteBalita } from '@/services/balita';

type IData = {
  headers: any[];
  items: any[];
  keyword: string;
}

const defData = () => ({
  headers: [
    {
      text: 'ID',
      value: 'id'
    },
    {
      text: 'Nama',
      value: 'nama'
    },
    {
      text: 'Umur',
      value: 'age'
    },
    {
      text: 'Berat Badan',
      value: 'beratBadan'
    },
    {
      text: 'Tinggi Badan',
      value: 'tinggiBadan'
    },
    {
      text: 'Jenis Kelamin',
      value: 'sex'
    }
  ],
  items: [],
  keyword: ''
});

export default Vue.extend({

  data: defData,

  computed: {
    filtered () {
      const keywordUpper = this.keyword.toUpperCase();
      return this.items.filter((it: any) => {
        return it.nama.toUpperCase().includes(keywordUpper);
      });
    }
  },

  methods: {

    async load_kriteria () {
      const resp = await axios.get('/kriteria');
      const kriteria = resp.data;
      console.log(kriteria);
      this.headers = [ ...this.headers, ...kriteria.map((k: any) => ({
        text: k.label,
        value: k.kode
      })),  {
          text: '',
          value: 'action'
        } ];

    },

    async loadDataBalita() {
      this.items = await apiListBalita();
    },

    async onDelete(id: number) {
      try {
        await apiDeleteBalita(id);
        alert('Sukses menghapus data balita');
      } catch (err) {
        alert('Gagal menghapus data balita');
        console.log(err);
      } finally {
        this.loadDataBalita();
      }
    }

  },

  mounted() {
    this.loadDataBalita();
    this.load_kriteria();
  }

});

